.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  z-index: 20;
  background-color: rgba(0, 0, 0, 0.75);
}
.modal {
  position: fixed;
  top: 15vh;
  left: 5%;
  width: 90%;
  max-height: 80%;
  /* min-height: 40%; */
  overflow-y: scroll;
  background-color: white;
  padding: 1rem;
  padding-bottom: 8px;
  border-radius: 14px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
  z-index: 30;
  animation: slide-down 300ms ease-out forwards;
}
.modal::-webkit-scrollbar{
  /* display: none; */
  width: 5px;
  /* height: 2px; */
}
.modal::-webkit-scrollbar-thumb{
  background-color: brown;
  border-radius: 3px;
}

@media (min-width: 768px) {
  .modal {
    width: 40rem;
    left: calc(50% - 20rem);
  }
}

@keyframes slide-down {
  from {
    opacity: 0;
    transform: translateY(-3rem);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}